export const fetchDataSummary = (value) => dispatch => {

    let data = value.summary ? value.summary.map(item => {
        return {
            id: item.summary_id,
            desc: item.title,
            used: item.used,
            isActive: false
        }
    }): []
    
    dispatch({
        type: JD_TEMPLATE_SAVE_SUMMARY_SUGGESTION_HANDLER,
        payload: data,
    });
};
